<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Security</h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                            <li class="breadcrumb-item"><router-link to="/console">Home</router-link></li>
                            <li class="breadcrumb-item active">Security</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">

                <form method="POST" @submit.prevent="submitForm">
                    
                    <div class="pb-5">
    
                        <div class="col-xl-6 col-md-12 mb-4">
                            <!-- XHR Response -->
                            <div id="response"></div>
                                
                            <div class="form-group text-start">
                                <label for="" class="">Old Password</label>
                                <input type="password" name="old_password" v-model="form.old_password" class="form-control remove-radius" placeholder="Enter your old password" required/>
                            </div>
                        </div>
                        <div class="col-xl-6 col-md-12 mb-4">
                            <div class="form-group text-start">
                                <label for="" class="">New Password</label>
                                <input type="password" name="new_password" v-model="form.new_password" class="form-control remove-radius" placeholder="Enter your new password" required/>
                            </div>
                        </div>
                        <div class="col-xl-6 col-md-12 mb-4">
                            <div class="form-group text-start">
                                <label for="" class="">Retype New Password</label>
                                <input type="password" name="r_new_password" v-model="form.r_new_password" class="form-control remove-radius" placeholder="Retype your new password" required/>
                            </div>
                        </div>

                        <div class="col-xl-6 col-md-12 mb-2 text-start">
                            <button class="btn btn-primary" type="submit"><i class="fa fa-save"></i> Save</button>
                        </div>

                    </div>

                </form>

            </section>
            <!-- /.content -->

        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";

import {mapActions, mapGetters} from 'vuex';

export default {
    name: "ConsoleSecurity",
    computed: mapGetters(['user']),
    data(){
        return {
            form: {
                old_password: '',
                new_password: '',
                r_new_password: '',
            }
        }
    },
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {
        ...mapActions(['update_password', 'user_account']),
        async submitForm(){
            try{
                await this.update_password({id: this.user.id, user: this.form});
                if (this.$store.getters.errorNo === 0) {
                    await this.user_account(this.user.id);
                    this.form.old_password = '';
                    this.form.new_password = '';
                    this.form.r_new_password = '';
                }
            }
            catch(err){
                console.log(err);
            }
        }
    },
    created(){
        this.user_account(this.user.id);
    }

}
</script>

